/* global console, document, Excel, Office */
// The initialize function must be run each time a new page is loaded
Office.onReady(() => {
  document.getElementById("sideload-msg").style.display = "none";
  document.getElementById("app-body").style.display = "flex";
  document.getElementById("save").onclick = run;
  document.getElementById("clear").onclick = clear;

  init();
});

async function init() {
  //process.env.KEY

  var api_key = await get_api_key();
  if (!api_key) {
    let myContainer = document.getElementById("api-key") as HTMLInputElement;
    myContainer.value = "Not Defined";
  } else {
    let myContainer = document.getElementById("api-key") as HTMLInputElement;
    myContainer.value = api_key;
  }
}
function clear() {
  OfficeRuntime.storage.removeItem("key");
}

function set_api_key(key) {
  OfficeRuntime.storage.setItem("key", key);
}

async function get_api_key() {
  var promise = await OfficeRuntime.storage.getItem("key");
  var api_key = promise;
  if (typeof api_key == "undefined") {
    throw new Error("SET API KEY");
  } else {
    return api_key;
  }
}

async function run() {
  try {
    var key = (<HTMLInputElement>document.getElementById("api-key")).value;

    set_api_key(key);

    await Excel.run(async (context) => {
      /**
       * Insert your Excel code here
       */
      const range = context.workbook.getSelectedRange();

      // Read the range address
      range.load("address");

      // Update the fill color
      range.format.fill.color = "yellow";

      var range2 = context.workbook.worksheets.getItem("Sheet1").getRange("A1");
      range2.values = [[await get_api_key()]];

      // await context.sync();
      // console.log(`The range address was ${range.address}.`);
    });
  } catch (error) {
    console.error(error);
  }
}
